import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import resizeBehavior from '@servicenow/behavior-resize';
import keyBindingBehavior from '@servicenow/behavior-key-binding';
import { t } from 'sn-translate';
import '@servicenow/now-card';
import '@servicenow/now-tabs';
import '@servicenow/now-alert';
import '@servicenow/now-popover';
import '@servicenow/now-empty-state';
import '@servicenow/now-heading';
import '../popover-content';

import '../sn-pa-target-list-item';

import { TAB_SELECTED, TARGET_CLICKED, RESET_FOCUS } from './constants';
import { TARGET_VALUE_CLICKED } from '../sn-pa-target-list-item/constants';

import styles from './sn-pa-target-list.scss';
import {
	applyFunctionToList,
	handleKeyNavigation,
	wrapWithHandler,
	getCurrentTarget,
} from './utils';
import {
	getDateValue,
	getTargetValueLabel,
} from '../sn-pa-target-list-item/utils';

function capitalizeFirstLetter(string) {
	return string.charAt(0).toUpperCase() + string.slice(1);
}

function getFilterMessage(filtersApplied) {
	if (filtersApplied.length > 1)
		return t(`Only ${capitalizeFirstLetter(filtersApplied[0])} and ${capitalizeFirstLetter(filtersApplied[1])} Target visible in the list. `);
	else
		return t(`Only ${capitalizeFirstLetter(filtersApplied[0])} Target visible in the list. `)
}


const view = (
	{
		properties: {
			hideFilters,
			hideTabs,
			showIndicatorName,
			listTitle,
			items,
			targetUnit,
			targetStatusMap,
			sortConfig,
			sortType,
			filters,
			type,
			hasMultipleElements,
		},
		behaviors,
		filters: filtersApplied = [],
		focusElement = 0,
		selectedTab = type || 'global',
	},
	{ updateState, dispatch }
) => {
	const { width } = behaviors.resize.size;
	const paTargetHandler = wrapWithHandler(items);
	let targetValueItems = paTargetHandler.getTargetValuesOfType(
		type || selectedTab
	);

	const appliedFilterConfig =
		(!hideFilters &&
			Array.isArray(filters) &&
			filters.filter((item) => filtersApplied.indexOf(item.id) !== -1)) ||
		[];

	if (appliedFilterConfig.length > 0) {
		let result = [];
		let _targetValueItems = [...targetValueItems];

		appliedFilterConfig.forEach((filterConfig) => {
			if (filterConfig.id === 'current') {
				const currentTarget = getCurrentTarget(_targetValueItems);
				if (currentTarget) result.push(currentTarget.sys_id);
			} else
				result = result.concat(
					_targetValueItems.filter(filterConfig.fn).map((item) => item.sys_id)
				);
		});

		targetValueItems = targetValueItems.filter(
			(item) => result.indexOf(item.sys_id) > -1
		);
	}

	if (!Array.isArray(targetValueItems))
		throw Error('Invalid target list items');

	targetValueItems = applyFunctionToList(
		targetValueItems,
		'sort',
		sortConfig,
		sortType
	);

	const listItems = targetValueItems.map((paTargetValue, index) => {
		const [startDate, reviewDate, value] = [
			getDateValue(paTargetValue.target_at),
			getDateValue(paTargetValue.target_review),
			paTargetValue.value,
		];

		return paTargetValue ? (
			<sn-pa-target-list-item
				tabIndex={focusElement === index || focusElement >= targetValueItems.length ? 0 : -1}
				targetUnit={targetUnit}
				aria-label={getTargetValueLabel(
					`${value} ${targetUnit || ''}`.trim(),
					startDate,
					reviewDate || t('No review date'),
					targetStatusMap &&
					targetStatusMap[paTargetValue.sys_id] &&
					targetStatusMap[paTargetValue.sys_id].text,
					targetStatusMap[paTargetValue.sys_id] &&
					targetStatusMap[paTargetValue.sys_id].value
				)}
				interaction="none"
				on-keydown={(e) => {
					const { nextFocus, clicked } = handleKeyNavigation(
						e,
						listItems,
						focusElement
					);
					if (nextFocus !== undefined) {
						e.preventDefault();
						updateState({ focusElement: nextFocus });
					} else if (clicked)
						dispatch(TARGET_VALUE_CLICKED, {
							paTargetValue,
							targetNode: e.target,
						});
				}}
				on-click={(e) => {
					updateState({ focusElement: index });
					dispatch(TARGET_VALUE_CLICKED, {
						paTargetValue,
						targetNode: e.target,
					});
				}}
				paTargetValue={paTargetValue}
				showIndicatorName={showIndicatorName}
				status={
					targetStatusMap && targetStatusMap[paTargetValue.sys_id]
				}
				role="option"
			/>
		) : undefined;
	});

	return (
		<div className="target-list__container">
			<div className="target-list__header">
				<slot name="pre-title">
					<div />
				</slot>

				<div className="target-list_info">
					<now-heading
						label={listTitle}
						variant="header-secondary"
						hasNoMargin
					/>
					<now-popover>
						<now-button-iconic
							icon="circle-info-outline"
							size="md"
							variant="tertiary"
							aria-describedby="targets-title-info"
							slot="trigger"
							bare
						/>
						<sn-pa-target-popover slot="content" info-text={t('Desired score for the KPI')} />
					</now-popover>
				</div>

				<div className="target-list__header-actions">
					<slot name="post-title" />
					<div className="filters-container">
						{Array.isArray(filters) && filters.length && !hasMultipleElements ? (
							<now-dropdown
								items={filters}
								icon="sliders-outline"
								variant="tertiary"
								size="md"
								hideCaret
								hideLabel
								select="multi"
								manageSelectedItems
								selectedItems={filtersApplied}
								tooltip-content={t('List setting')}
								panel-fit-props={{
									position: ['bottom-right'],
								}}
								config-aria={{
									trigger: {
										'aria-label': t('List setting'),
									},
								}}
							/>
						) : undefined}
					</div>
				</div>
			</div>
			<div slot="target-filter-info">
				{filtersApplied && filtersApplied.length < 3 ? (<now-alert
					status="info"
					icon="info-circle-outline"
					content={{
						type: 'jsx', value: (
							<div>
								{getFilterMessage(filtersApplied)}
								<now-text-link label={t('Show all targets in the list')} href="javascript:void(0)" variant="primary" underlined configAria={{
									'aria-label': t(
										'Show all targets in the list'
									),
								}}></now-text-link>
							</div>
						)
					}}
					append-to-payload={{ type: 'dismiss' }}
					action={{ type: 'dismiss' }}></now-alert>) : ''}
			</div>
			<slot name="target-filter-info" />
			<slot name="pre-list">
				<div />
			</slot>
			{hasMultipleElements ?
				(
					<div className="has-multiple-element">
						<now-empty-state
							type="not-configured"
							variant="medium"
							headerText={t('Targets are not available for selected filters.')}
						/>
					</div>
				) : ''}
			{!hasMultipleElements && items && !items.length ? (
				<now-empty-state
					type="not-configured"
					variant="medium"
					headerText={t('No targets are created yet.')}
				/>
			) : (
					(!type || type !== 'all') && !hideTabs && !hasMultipleElements ? (
						<now-tabs
							fixed-width={width < 320}
							append-to-payload={{
								type: TAB_SELECTED,
							}}
							items={[
								{ id: 'global', label: t('Global') },
								{ id: 'personal', label: t('Personal') },
							]}
							manageSelectedItem
							selectedItem={type || selectedTab}
						/>
					) : undefined
				)
			}
			<div className="target-list__items-overflow">
				<div
					class={{
						'target-list__items': true,
						single: width > 977 && width < 1392,
						'two-by-three': width > 609 && width < 912,
					}}
				>
					<slot name="placeholder">
						{hasMultipleElements ? '' : (
							items && items.length && targetValueItems.length === 0 ? (
								<now-empty-state
									type="not-configured"
									variant="medium"
									headerText={t('No {0} targets are created yet.', selectedTab)}
								/>
							) : (
									listItems
								)
						)
						}
					</slot>
				</div>
			</div>
			<slot name="post-list">
				<div />
			</slot>
		</div>
	);
};

createCustomElement('sn-pa-target-list', {
	renderer: {
		type: snabbdom,
	},
	initialState: {
		filters: ['current', 'past', 'future'],
	},
	properties: {
		listTitle: {
			schema: {
				type: 'string',
			},
			default: t('Targets'),
		},
		items: {},
		targetUnit: {
			schema: {
				type: 'string',
			},
		},
		type: {
			schema: {
				type: 'string',
				enum: ['global', 'personal', 'all'],
			},
		},
		hideTabs: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		hideFilters: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		showIndicatorName: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		hasMultipleElements: {
			schema: {
				type: 'boolean',
			},
		},
		targetType: {
			schema: {
				type: 'string',
				enum: ['global', 'personal', 'all'],
			},
		},
		targetStatusMap: {
			schema: {
				type: 'object',
			},
			default: {},
		},
		filters: {
			schema: {
				type: 'array',
				properties: {
					label: {
						type: 'string',
					},
					id: {
						type: 'string',
					},
					fn: {},
				},
				required: ['label', 'id', 'function'],
			},
			default: [],
		},
		defaultFilters: {
			schema: {
				type: 'array',
			},
		},
		role: {
			default: 'listbox',
			reflect: true,
		},
		resetFocusOnBlur: {
			schema: {
				type: 'boolean',
			},
			default: false,
			reflect: true,
		},
	},
	actionHandlers: {
		[TARGET_VALUE_CLICKED]: ({
			action: {
				payload: { paTargetValue, targetNode },
				stopPropagation,
			},
			state: {
				properties: { paTarget, selectedItem: selectedItemProp },
				selectedItem,
			},
			dispatch,
			updateState,
		}) => {
			stopPropagation();
			dispatch(TARGET_CLICKED, { paTarget, paTargetValue, targetNode });
			if (!selectedItemProp) {
				updateState({
					selectedItem:
						paTargetValue.sys_id === selectedItem
							? ''
							: paTargetValue.sys_id,
				});
			}
		},
		[RESET_FOCUS]: ({
			action: { stopPropagation },
			updateState,
			properties: { resetFocusOnBlur },
		}) => {
			stopPropagation();
			if (resetFocusOnBlur) updateState({ focusElement: 0 });
		},
		'NOW_TABS#SELECTED_ITEM_SET': ({
			action: {
				payload: { value, type },
			},
			properties: { type: typeProp },
			dispatch,
			updateState,
		}) => {
			dispatch(type, { value });
			if (!typeProp) updateState({ selectedTab: value });
		},
		'NOW_DROPDOWN#SELECTED_ITEMS_SET': ({
			action: {
				payload: { value },
			},
			updateState,
		}) => {
			if (value.length) value.length && updateState({ filters: value });
		},
		'NOW_TEXT_LINK#CLICKED': ({ updateState }) => {
			updateState({ filters: ['past', 'current', 'future'] });
		},
		'NOW_ALERT#ACTION_CLICKED': ({
			action: {
				payload: { type },
			},
			updateState,
		}) => {
			if (type === 'dismiss')
				updateState({ filters: ['past', 'current', 'future'] });
		},
	},
	view,
	behaviors: [
		{ behavior: rtlBehavior },
		{
			behavior: resizeBehavior,
			options: { timer: { type: 'throttle', wait: 320 } },
		},
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					onTabKeyPress: 'Tab',
				},
				handlers: {
					onTabKeyPress: function (host, event, dispatch) {
						dispatch(RESET_FOCUS);
					},
				},
			},
		},
	],
	styles,
});
