import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import rtlBehavior from '@servicenow/behavior-rtl';
import { t } from 'sn-translate';
import '@servicenow/now-card';
import '@servicenow/now-label-value';

import { TARGET_VALUE_CLICKED, TARGET_VALUE_SELECTED } from './constants';
import {
	getDateValue,
	getTargetValueLabel,
	getVisibleDateFormatted,
} from './utils';

import styles from './sn-pa-target-list-item.scss';
import { targetStatusSchema } from './schema';

const getTarget = (value) => (
	<now-label-value-stacked
		items={[
			{
				label: t('Target'),
				value: { type: 'string', value: `${value}` },
			},
		]}
		itemMinWidth="50%"
		delimiter=","
		size="sm"
		align="horizontal-equal"
	></now-label-value-stacked>
);

const getStartDate = (date, visibleDate, reviewDate) => (
	<now-label-value-stacked
		items={[
			{
				label: t('Start date'),
				value: { type: 'string', value: `${date}` },
			},
			{
				label: t('Review Date'),
				value: {
					type: 'string',
					value: `${reviewDate ? visibleDate : '-'}`,
				},
			},
		]}
		itemMinWidth="50%"
		delimiter=","
		size="sm"
		align="horizontal-equal"
	></now-label-value-stacked>
);

const view = ({
	properties: {
		paTargetValue,
		showIndicatorName,
		selected,
		manageSelected,
		status = {},
		interaction,
	},
}) => {
	if (!paTargetValue) throw Error('Invalid PA Target provided');
	const { text, value: statusValue } = status;

	const [startDate, reviewDate, value, indicatorName] = [
		getDateValue(paTargetValue.target_at),
		getDateValue(paTargetValue.target_review),
		paTargetValue.value,
		paTargetValue.target.indicator.name
	];

	const visibleDate = getVisibleDateFormatted(startDate);
	const visibleReviewDate = reviewDate
		? getVisibleDateFormatted(reviewDate)
		: t('No review date');

	return (
		<now-card
			selected={selected}
			manage-selected={manageSelected}
			className="pa-target-list__item-card"
			class={{
				[`type-${interaction}`]: true,
			}}
			size="md"
			config-aria={{
				'aria-label': getTargetValueLabel(
					value,
					startDate,
					reviewDate,
					text,
					statusValue
				),
			}}
			append-to-payload={{ paTargetValue }}
			variant="primary"
			interaction={interaction}
		>
			<div className="pa-target-list__item-container">
				{showIndicatorName ?
					(
						<label className="target-indicator-label"
						>{t('{0}', indicatorName)}</label>
					)
					: ''}
				{getTarget(value)}
				{getStartDate(visibleDate, visibleReviewDate, reviewDate)}
			</div>
		</now-card>
	);
};

createCustomElement('sn-pa-target-list-item', {
	renderer: {
		type: snabbdom,
	},
	properties: {
		paTargetValue: {
			schema: {
				type: 'object',
			},
		},
		showIndicatorName: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		status: {
			schema: targetStatusSchema,
		},
		manageSelected: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		selected: {
			schema: {
				type: 'boolean',
			},
			default: false,
		},
		interaction: {
			default: 'none',
			schema: {
				type: 'string',
				enum: ['none', 'select', 'click'],
			},
		},
	},
	view,
	actionHandlers: {
		'NOW_CARD#SELECTED_SET': ({
			action: {
				payload: { paTargetValue },
				stopPropagation,
			},
			dispatch,
		}) => {
			stopPropagation();
			dispatch(TARGET_VALUE_SELECTED, { paTargetValue });
		},
		'NOW_CARD#CLICKED': ({
			action: {
				payload: { paTargetValue },
				stopPropagation,
			},
			dispatch,
		}) => {
			stopPropagation();
			dispatch(TARGET_VALUE_CLICKED, { paTargetValue });
		},
	},
	behaviors: [{ behavior: rtlBehavior }],
	styles,
});
