import get from 'lodash/get';
import moment from 'moment';
import { TARGET_DATE_FORMAT } from '../sn-pa-target-list-item/constants';

export function applyFunctionToList(list, listFn, fnConfig, type) {
	if (
		list &&
		typeof list[listFn] === 'function' &&
		fnConfig &&
		type &&
		fnConfig[type] &&
		typeof fnConfig[type] === 'function'
	)
		return list[listFn](fnConfig[type]);
	return list;
}

export function isCurrentTarget(item, list) {
	const currentTarget = getCurrentTarget(list);
	return currentTarget && item && currentTarget.sys_id === item.sys_id;
}

export function getCurrentTarget(list) {
	if (!Array.isArray(list)) return;

	let foundCurrent = false;
	const currentTarget = list.find((a) => {
		const isNotFuture =
			!foundCurrent &&
			moment(a.target_at, TARGET_DATE_FORMAT) <= moment();
		if (isNotFuture && !foundCurrent) foundCurrent = true;

		return foundCurrent && isNotFuture;
	});
	return currentTarget;
}

export function handleKeyNavigation(e, items, focusElement) {
	let nextFocus;
	switch (e.key) {
		case 'ArrowDown':
			if (focusElement + 1 < items.length && items[focusElement + 1]) {
				items[focusElement + 1].elm.focus();
				nextFocus = focusElement + 1;
			}
			break;
		case 'ArrowUp':
			if (focusElement - 1 > -1 && items[focusElement - 1]) {
				items[focusElement - 1].elm.focus();
				nextFocus = focusElement - 1;
			}
			break;
		case ' ':
		case 'Enter':
			e.preventDefault();
			return { clicked: true };
	}
	return { nextFocus };
}

export const wrapWithHandler = (obj = []) => {
	if (typeof obj !== 'object') {
		// eslint-disable-next-line no-console
		console.error(Error(`Handler got non-object to proxy :::-> <${obj}>`));
		obj = [];
	}

	return new Proxy(obj, {
		get: function (target, prop) {
			switch (prop) {
				case 'target_values':
					return target;
				case 'getTargetValuesOfType':
					return function (type = 'global') {
						if (type === 'all') return this.target_values;
						return this.getTargetsOfType(type) || [];
					};
				case 'getTargetInfo':
					return function (targetValueSysId) {
						if (Array.isArray(this))
							return this.find((target) => {
								return (
									target.target_values.find((value) => {
										value.sys_id === targetValueSysId;
									}) !== -1
								);
							});
					};
				case 'getTargetsOfType':
					return function (type = 'all') {
						if (Array.isArray(target))
							return target.filter(
								(_target) =>
									(type === 'global' &&
										!get(_target, 'target.owner.sys_id')) ||
									(type === 'personal' &&
										get(_target, 'target.owner.sys_id')) ||
									type === 'all'
							);
					};
				default:
					return Reflect.get(...arguments);
			}
		},
	});
};
