import { t } from 'sn-translate';
import get from 'lodash/get';
import moment from 'moment';
import { TARGET_DATE_FORMAT, TARGET_DATE_VISIBLE_FORMAT } from './constants';
export function getVisibleDateFormatted(date) {
	moment.locale(
		get(
			window,
			'ux_globals.presource["sn-workspace-header:wsUserData"].data.GlideDomain_Query.user.language',
			'en'
		)
	);
	const momentDate = moment(`${date}`, TARGET_DATE_FORMAT);
	if (momentDate.isValid())
		return momentDate.format(TARGET_DATE_VISIBLE_FORMAT);
	return date;
}

export function getTargetValueLabel(
	value,
	date,
	reviewDate,
	statusText,
	statusValue
) {
	return `${t('Target value {0}', [value])}, ${t('Start date {0}', [
		getVisibleDateFormatted(date),
	])}, ${t('Review date {0}', [getVisibleDateFormatted(reviewDate)])}${
		statusText ? `, ${statusText}` : ''
	}${statusValue ? `, ${statusValue}` : ''}`.trim();
}

export function getDateValue(date) {
	return date ? date : undefined;
}
