import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';

const view = ({
    properties: {
        infoText
    },
}) => {

    return (
        <div className="target-info-text">
            <span role="status" aria-live="assertive">{infoText}</span>
        </div>)
};

createCustomElement('sn-pa-target-popover', {
    renderer: {
        type: snabbdom,
    },
    properties: {
        infoText: {
            schema: {
                type: 'string'
            }
        }
    },
    view,
    styles,
});
